@import 'styles/site.sass';

.Main {
  cursor: pointer;
  color: $black;
  display: block;
  position: relative;
  margin-bottom: $space-l;

  .container {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: $space-s; }

  .content {
    max-width: $text-max-width; }

  p {
    @extend %body-m; }

  h5 {
    @extend %body-m;
    @extend %medium; }

  h2 {
    @extend %heading-l; }

  .backer {
    @include background-block($peach-200, -units(6)); }

  header {
    margin-bottom: units(3);
    @include animate(0.3s);
    background: url(../../images/icons/icon-navigation-arrow.png) no-repeat center right;
    background-size: auto units(3); }

  &:hover {
    .backer {
      opacity: 1; }
    header {
      background-position: right units(3) center;
      text-decoration: underline; } } }
